nav#new-nav{
    position: absolute;
    width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 20px !important;
    /*
    max-height:120px;
    */

    min-height: var(--navbar-height) !important;
    border-top: 2px solid rgba(0, 0, 0, 0.2);

    transition: all 0.3s ease-in-out;
    overflow-y: visible;
    z-index: 1500;
}
nav#new-nav a {
    color:var(--nav-item-color);
    text-shadow: -0.5px 0.5px 1px rgba(0, 0, 0, 0.8);

}
.nav-blurbox{
    position: absolute;
    left:-5px;
    top:-10px;
    bottom:0px;
    width:100%;
    filter:blur(2px) !important;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: -1;
    transition: opacity 2s ease-in-out;
}

/* NAV-ITEMS */
nav#new-nav .nav-item {
    margin:0 8px;
    font-size: 14px;
    transition: all 0.2s ease-in;
    cursor: pointer;
}
nav#new-nav .nav-item:hover {
    color:rgba(255, 255, 255, 1);
    /*
    color:rgba(255, 255, 255, 0.95);

    */
}

/* MIDDLE PAGES */
nav#new-nav .nav-pages{
    display: flex;
    justify-content: center;
    align-items: center;
}

/*RIGHT ACTIONS*/


nav#new-nav > .nav-actions {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    min-width:25% ; 
}

nav#new-nav > .actions{
    cursor: pointer;
}

nav#new-nav .nav-item.logout {
    margin-right: 18px;
}
