#spinner-box {
    position: relative;
    width: 100%;
    height:100%;
    min-width: 150px;
    min-height: 150px !important;
    background-color: rgb(77, 74, 74);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#bars4 {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  height: 50px;
  width: 50px;
  margin: -25px 0 0 -25px;
}

#bars4 span {
  display: block;
  bottom: 10px;
  width: 9px;
  height: 15px;
  background: rgba(0, 0, 0, 0.25);
  position: absolute;
  -webkit-animation: bars4 2s  infinite ease-in-out;
          animation: bars4 2s  infinite ease-in-out;
}

#bars4 span:nth-child(2) {
  left: 11px;
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

#bars4 span:nth-child(3) {
  left: 22px;
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

#bars4 span:nth-child(4) {
  left: 33px;
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

#bars4 span:nth-child(5) {
  left: 44px;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

@keyframes bars4 {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    background: rgba(0, 0, 0, 0.25);
  }
  25% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    background: #000000;
  }
  50% {
    -webkit-transform: translateX(15px);
            transform: translateX(15px);
    -webkit-transform: translateX(15px);
            transform: translateX(15px);
    background: rgba(0, 0, 0, 0.25);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    background: rgba(0, 0, 0, 0.25);
  }
}
@-webkit-keyframes bars4 {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    background: rgba(0, 0, 0, 0.25);
  }
  25% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    background: #000000;
  }
  50% {
    -webkit-transform: translateX(15px);
            transform: translateX(15px);
    background: rgba(0, 0, 0, 0.25);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    background: rgba(0, 0, 0, 0.25);
  }
}

.fade-in {
	opacity: 1;
	animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: 2s;
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
:root{
    --g-col-gap: auto;
    --g-col-gap: 2vw;
    --g-row-gap: 5vh;

    --g-box-max:90vw;
    --g-box-min:75vw;
    --g-width-1:calc((var(--g-box-min) / 1));
    --g-width-1:calc((var(--g-box-min) / 1));



    --g-col-1: repeat(1, minmax(var(--g-box-min), var(--g-box-max)));
    --g-col-2: repeat(2 ,
        minmax(
            calc((var(--g-box-min) / 2) - ( 1 * var(--g-col-gap)  )),
            calc((var(--g-box-max) / 2) - ( 1 * var(--g-col-gap)  ))
            ));
    --g-col-3: repeat(3 ,
        minmax(
            calc((var(--g-box-min) / 3) - ( 1 * var(--g-col-gap)  )),
            calc((var(--g-box-max) / 3) - ( 1 * var(--g-col-gap)  ))
            ));
    --g-col-4: repeat(4 ,
        minmax(
            calc((var(--g-box-min) / 4) - ( 1 * var(--g-col-gap)  )),
            calc((var(--g-box-max) / 4) - ( 1 * var(--g-col-gap)  ))
            ));
    --g-col-5: repeat(5 ,
        minmax(
            calc((var(--g-box-min) / 5) - ( 1 * var(--g-col-gap)  )),
            calc((var(--g-box-max) / 5) - ( 1 * var(--g-col-gap)  ))
            ));
    --g-col-6: repeat(6 ,
        minmax(
            calc((var(--g-box-min) / 6) - ( 1 * var(--g-col-gap)  )),
            calc((var(--g-box-max) / 6) - ( 1 * var(--g-col-gap)  ))
            ));
    --g-col-8: repeat(8 ,
        minmax(
            calc((var(--g-box-min) / 8) - ( 1 * var(--g-col-gap)  )),
            calc((var(--g-box-max) / 8) - ( 1 * var(--g-col-gap)  ))
            ));
    --g-col-10: repeat(10 ,
        minmax(
            calc((var(--g-box-min) / 10) - ( 1 * var(--g-col-gap)  )),
            calc((var(--g-box-max) / 10) - ( 1 * var(--g-col-gap)  ))
            ));
    --g-col-12: repeat(12 ,
        minmax(
            calc((var(--g-box-min) / 12) - ( 1 * var(--g-col-gap)  )),
            calc((var(--g-box-max) / 12) - ( 1 * var(--g-col-gap)  ))
            ));
}


