/*----------GENERAL---------*/
.g-box{
    /*
    background-color: rgb(170, 231, 112);
    */
    display: grid;
    grid-column-gap: var(--g-col-gap) !important;
    grid-row-gap: var(--g-row-gap);
    padding: 20px 0 30px 0;
    width:100% !important;
}

.g-item{
    /*
    background-color: rgb(165, 104, 29);
    padding: 0 !important;
    */
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 100%;
    overflow: hidden;
    transition: ease-in-out;
    transition-duration: 0.6s;
}
.g-item-card {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 100%;
    border-radius: 8px;
    overflow: hidden;
    padding:4px;
    background-color: var(--color-light);
    box-shadow:
        rgba(40, 40, 40, 0.45) 0px 1px 5px,
        rgba(50, 50, 50, 0.05) 0px 1px 10px,
        rgba(60, 0, 100, 0.08) 0px 20px 30px;
    /*
    box-shadow: 8px 30px 29px -8px rgba(0, 0, 0, 0.57) ;
    border: 1px solid rgba(0, 0, 0, 0.47);
    */

}

.g-item.hover:hover {
    transform:translateY(-15px);
    -webkit-transform: translateY(-15px);
    -moz-transform:translateY(-15px);
}
.g-item img {
    max-width: 100%;
} 
.g-item.shadow:hover {
    box-shadow: 8px 30px 29px -8px rgba(0, 0, 0, 0.97) ;
}

.g-box .g-item p{
    overflow: hidden;
    text-overflow: ellipsis;
}

.g-item .children-box {
    position: relative;
    top:0;
    bottom:0;
    padding:4px;
    background-color: var(--color-light);
}
.g-box.xs .g-item p,
.g-box.s .g-item p{
    font-size: 12px;
}
