} 
  
input.sp-text-input[type=text]:focus  :-ms-input-placeholder { /* Internet Explorer 10-11 */ 
    color: var(--active-color); 
    border-color:white;
} 
  
input.sp-text-input[type=text]:focus  ::-ms-input-placeholder { /* Microsoft Edge */ 
    color: var(--active-color); 
    border-color:white;
} 

