/* ACTIONS*/
section.pixly-auth{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color:var(--landing-color-active);
    color: var(--landing-color-primary);
    width:100%;
    padding:5vw;
    border-bottom:1px solid grey;
}

section.pixly-auth h2{
    font-weight: 700;
    color: var(--landing-color-primary);
    text-transform: uppercase;
}

section.pixly-auth .action.button {
    width:150px;
    height:50px;
    margin:20px;
    cursor: pointer;
    transition:  .15s ease-in;
    border-radius:var(--space-x);
    box-sizing: border-box;
    font-weight: 600;
}
.action.button.active {
    background: hsl(236, 59%, 49%);
    border: 3px solid var(--landing-color-primary);
    color:hsl(0, 0%, 100%);
}
.action.button.active:hover{
    color:hsl(236, 59%, 49%);
    background: hsl(0, 0%, 100%);
    border: 3px solid hsl(236, 59%, 60%);
}


.action.button.primary {
    background: hsl(0, 0%, 100%);
    border: 3px solid hsl(236, 59%, 49%);
    color:hsl(236, 59%, 49%);
}
.action.button.primary:hover{
    box-shadow: 0px 10px 15px -8px rgba(0, 0, 0, 0.57) ;
}


