:root{
    --content-space-s:75vw;
    --content-space-m:85vw;
    --content-space-l:95vw;

    --content-space: var(--content-space-m);
    --col-gap: var(--space-4x);
    --row-gap: var(--space-2x);
}

.grid-container{
    display: grid;
    grid-template-columns: auto;
    grid-column-gap: var(--col-gap);
    grid-row-gap: var(--row-gap);
    grid-template-columns: auto auto auto auto;
    padding-top: 20px;
}
.grid-item{
    width: 100%;
    height:0;
    content: "";
    padding-bottom: calc(100% * 1);
    /*
    */
    overflow: hidden;
    position: relative;
    display: block;
}

.grid-item .bottom-panel{
    position: relative;
    bottom:0;
    left:0;
    min-height: 30px;
}

.grid-container.poster > .grid-item{
    max-width: 185px;
}
/*
img.grid-image{
    min-width: 100% !important;
    min-height:100% !important;
    object-fit: cover;
}

*/

img.grid-image{
    position: relative;
    top:0;
    left:0;
    min-width: 100%;
    height:auto;

}

.cover-hover-box{
    position: absolute;
    top:10%;
    left:0;
    min-width: 100%;
    background-color: rgba(0,0,0, 0.9);
}
